/*=====================
  Базовая типография
=====================*/

@font-face {
	font-family: "Proxima-bold";
	font-weight: normal;
	font-style: normal;
	src: local("Proxima-bold"), url("../font/proxima_nova_bold-webfont.woff2") format("woff2"), url("../font/proxima_nova_bold-webfont.woff") format("woff"), url("../font/proxima_nova_bold-webfont.ttf") format("truetype");
}

@font-face {
	font-family: "Proxima";
	font-weight: normal;
	font-style: normal;
	src: local("Proxima"), url("../font/proxima_nova_regular-webfont.woff2") format("woff2"), url("../font/proxima_nova_regular-webfont.woff") format("woff"), url("../font/proxima_nova_regular-webfont.ttf") format("truetype");
}

body {
	color: #000000;
	font-family: "Proxima", Arial, sans-serif;
	font-size: 15px;
	font-weight: 400;
	line-height: 20px;
}

.btn {
	display: inline-block;
	box-sizing: border-box;
	padding: 22px 90px;
	outline: none;
	border: none;
	background-color: #ffffff;
	color: #000000;
	cursor: pointer;
	transition: background-color 0.1s linear;
	text-align: center;
	vertical-align: top;
	text-decoration: none;
	font-size: 20px;
	line-height: 39px;
}

.btn:hover {
	background-color: #ffca28;
}

input[type="text"],
textarea,
select,
input[type="password"] {
	box-sizing: border-box;
	width: 100%;
	outline: none;
	font-family: "Proxima", Arial, sans-serif;
	font-size: 18px;
	line-height: 20px;
}

label {
	cursor: pointer;
}

button {
	outline: none;
	cursor: pointer;
}

/*=== компоненты ===*/

.news {
	width: 300px;
	margin: 50px auto 0;
}

.news__section {
	position: relative;
}

.news__title {
	margin: 0;
	margin-bottom: 20px;
	padding: 0 0 11px;
	border-bottom: 1px solid #e1e1e1;
	letter-spacing: 2px;
	font-size: 20px;
	line-height: 20px;
}

.news__all-link {
	position: absolute;
	top: 0;
	right: 0;
	color: #c0c0c0;
	transition: color 0.2s ease;
	text-decoration: none;
	text-transform: uppercase;
	font-family: "Proxima-bold", Arial, sans-serif;
	font-size: 11px;
	line-height: 25px;
	will-change: color;
}

.news__all-link:hover {
	color: #000000;
}

.news__container {
	position: relative;
}

.news__container::before {
	position: absolute;
	left: 20px;
	z-index: -1;
	width: 1px;
	height: 100%;
	background-color: #e1e1e1;
	content: "";
}

.news__before {
	display: inline-block;
	vertical-align: top;
	margin-top: 7px;
	position: relative;
	padding-left: 50px;
	color: #000000;
	transition: opacity 0.2s ease;
	text-decoration: none;
	font-family: "Proxima-bold", Arial, sans-serif;
	font-size: 14px;
	line-height: 21px;
	opacity: 0.2;
	will-change: opacity;
}

.news__before::before {
	position: absolute;
	top: -3px;
	left: 8px;
	width: 21px;
	height: 21px;
	border: 1px solid #000000;
	border-radius: 50%;
	background-image: url("../img/arrow.svg");
	background-repeat: no-repeat;
	background-position: center;
	content: "";
}

.news__before:hover {
	opacity: 1;
}

.news-item {
	display: flex;
	padding-bottom: 25px;
	outline: none;
	color: #000000;
	cursor: pointer;
	text-decoration: none;
	align-items: flex-start;
	justify-content: space-between;
}

.news-item:last-child {
	padding-bottom: 20px;
}

.news-item--more-text {
	padding-bottom: 15px;
}

.news-item__time {
	padding: 0 3px 3px 5px;
	background-color: #ffffff;
	transition: color 0.2s ease;
	text-align: center;
	font-family: "Proxima-bold", Arial, sans-serif;
	font-size: 14px;
	line-height: 14px;
	will-change: color;
}

.news-item__time span {
	display: block;
	color: #a1a1a1;
	transition: color 0.2s ease;
	font-family: Arial, sans-serif;
	font-size: 11px;
	line-height: 11px;
	will-change: color;
}

.news-item__picture {
	overflow: hidden;
	width: 50px;
	height: 50px;
	border-radius: 3px;
}

.news-item__text {
	width: 185px;
	margin: 0;
	transition: color 0.2s ease;
	transform: translateY(-4px);
	will-change: color;
}

.news-item:hover .news-item__text {
	color: #0aaf86;
	text-decoration: underline;
}

.news-item:hover .news-item__time {
	color: #0aaf86;
}

.news-item:hover .news-item__time span {
	color: #0aaf86;
}

.news-item:hover .news-item__picture {
	position: relative;
}

.news-item:hover .news-item__picture::before {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border-radius: 3px;
	background-color: rgba(0, 0, 0, 0.2);
	content: "";
	box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.1);
}

